// 我的考试页面
<template>
  <div id="myExam">
    <div class="title">我的练习</div>
    <div class="wrapper">
      <el-menu
  :default-active="activeIndex2"
  class="el-menu-demo"
  mode="horizontal"
  @select="handleSelect"
  background-color="#545c64"
  text-color="#fff"
  active-text-color="#ffd04b">
  <el-menu-item index="1"><el-badge :value="all" class="item" type="primary" >全部</el-badge></el-menu-item>
  <el-menu-item index="2"><el-badge :value="no_start" class="item" type="primary">未开始</el-badge></el-menu-item>
  <el-menu-item index="3"><el-badge :value="already_start"  class="item" type="primary">已开始</el-badge></el-menu-item>
  <el-menu-item index="4"><el-badge :value="end_start"  class="item">已过期</el-badge></el-menu-item>
</el-menu>
      <ul class="top">
        <!-- <li class="order" >
          <el-badge :value="all" class="item" type="primary" >
            <span @click="search(0)">全部&nbsp;
            </span>
          </el-badge>
        </li>
        <li class="order">
          <el-badge :value="no_start" class="item" type="primary">
            <span @click="search(1)">未开始&nbsp;</span>
          </el-badge>
        </li>
        <li class="order">
          <el-badge :value="already_start"  class="item" type="primary">
            <span @click="search(2)">已开始&nbsp;</span>
          </el-badge>
        </li>
        <li class="order">
          <el-badge :value="end_start"  class="item">
            <span @click="search(3)">已过期&nbsp;</span>
          </el-badge>
        </li> -->

        <li class="search-li"><div class="icon"><input type="text" placeholder="试卷名称" class="search" v-model="key"><i class="el-icon-search"></i></div></li>
        <li><el-button type="primary" @click="search(0)">搜索试卷</el-button></li>
      </ul>
      <ul class="paper" v-loading="loading">
        <li class="item" v-for="(item,index) in pagination.records" :key="index">
          <h4 @click="toExamMsg(item.examCode)">{{item.source}}</h4>
          <p class="name">{{item.source}}-{{item.description}}</p>
          <div class="info">
            <i class="el-icon-loading"></i><span>{{item.examDate.substr(0,10)}}</span>
            <i class="iconfont icon-icon-time"></i><span v-if="item.totalTime != null">限时{{item.totalTime}}分钟</span>
            <i class="iconfont icon-fenshu"></i><span>满分{{item.totalScore}}分</span>
          </div>
        </li>
      </ul>
      <div class="pagination">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="pagination.current"
          :page-sizes="[6, 10, 20, 40]"
          :page-size="pagination.size"
          layout="total, sizes, prev, pager, next, jumper"
          :total="pagination.total">
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  // name: 'myExam'
  data() {
    return {
      loading: false,
      key: null, //搜索关键字
      allExam: null, //所有考试信息
      all:'',
      no_start:'',
      already_start:'',
      end_start:'',
      pagination: { //分页后的考试信息
        current: 1, //当前页
        total: null, //记录条数
        size: 6 //每页条数
      },
      pattern:'',
      activeIndex2: '1'
    }
  },
  created() {
    this.getExamInfo()
    this.loading = true
    this.getInfo()
  },
  // watch: {
    
  // },
  methods: {
    handleSelect(key) {
        if(key==='1')
          this.search(0)
        else if(key==='2')
          this.search(1)
        else if(key==='3')
          this.search(2)
        else if(key==='4')
          this.search(3)
      },
    getInfo(){
      this.$axios({
        url: '/${process.env.examApi}/exam/all',
        method: 'get',
      }).then(res => {
        if(res.data.code ==200) {
          this.all=res.data.data.allNumber;
          this.no_start=res.data.data.noStartNumber;
          this.already_start=res.data.data.alreadyNumber;
          this.end_start=res.data.data.endStartNumber;
        }

      })
    },
    //获取当前所有考试信息
    getExamInfo() {
      this.$axios(`/${process.env.examApi}/exams/${this.pagination.current}/${this.pagination.size}`).then(res => {
        this.pagination = res.data.data
        this.loading = false
        console.log(this.pagination)
      }).catch(error => {
        console.log(error)
      })
    },
    //改变当前记录条数
    handleSizeChange(val) {
      this.pagination.size = val
      this.getExamInfo()
    },
    //改变当前页码，重新发送请求
    handleCurrentChange(val) {
      this.pagination.current = val
      this.getExamInfo()
    },
    //搜索试卷
    // search() {
    //   this.$axios('/api/exams').then(res => {
    //     if(res.data.code == 200) {
    //       let allExam = res.data.data
    //       let newPage = allExam.filter(item => {
    //         return item.source.includes(this.key)//搜索关键字，相当于过滤，得重写
    //       })
    //       this.pagination.records = newPage
    //     }
    //   })
    // },
    search(param) {
      this.$axios({
        url:'/${process.env.examApi}/exams',
        method:'put',
        data:{
          type:param,  //0,1,2,3,4
          key:this.key
        }
      }).then(res => {
        if(res.data.code == 200) {
          console.log(res.data.data)
          this.pagination.records = res.data.data
          if(param==0)
            this.pattern='全部'
          else if(param ==1)
            this.pattern='未开始'
          else if(param==2)
            this.pattern='已开始'
          else
            this.pattern='已过期'
        }
      })
    },
    //跳转到试卷详情页
    toExamMsg(examCode) {
      this.$router.push({path: '/examMsg', query: {examCode: examCode}})
      console.log(examCode)
    }
  }
}
</script>


<style lang="less" scoped>
.pagination {
  padding: 20px 0px 30px 0px;
  .el-pagination {
    display: flex;
    justify-content: center;
  }
}
